interface ButtonBadgeProps extends React.ButtonHTMLAttributes<HTMLButtonElement> {
  text: string;
  typeIcon: 'schedule' | 'result';
}
const ButtonBadge: React.FC<ButtonBadgeProps> = ({ text, typeIcon, ...props }) => {
  return (
    <button
      {...props}
      className={clsx(
        'p-[10px] flex items-center space-x-[10px] rounded-[10px] border active:scale-95 duration-150 min-w-48 max-w-52',
        typeIcon === 'schedule' ? 'bg-[#F7FFF5] border-green-700' : 'bg-[#F7FAFF] border-blue-700'
      )}
    >
      {typeIcon === 'schedule' ? <CalendarIcon /> : <ResultTestIcon />}

      <p className="text-xs text-gray-600">{text}</p>
    </button>
  );
};

function FooterCard() {
  const [isOpenApplicationProgress, setIsOpenApplicationProgress] = React.useState<boolean>(true);
  const [isOpenHistory, setIsOpenHistory] = React.useState<boolean>(false);

  const handlerOpenApplicationProgress = () => {
    setIsOpenApplicationProgress(true);
  };

  const handlerCloseApplicationProgress = () => {
    setIsOpenApplicationProgress(false);
  };

  return (
    <footer className="mt-5 pt-3 border-t">
      {!isOpenApplicationProgress && (
        <div className="flex items-center space-x-2">
          <button onClick={handlerOpenApplicationProgress} className="text-blue-700 text-sm font-semibold">
            See Application Progress
          </button>
          <p></p>
          <button className="text-gray-700 text-sm font-semibold"> View History</button>
        </div>
      )}

      {isOpenApplicationProgress && (
        <div className="pt-7 ml-16 -mr-16">
          <StepperApplication />
        </div>
      )}
    </footer>
  );
}

function StepperApplication() {
  const steps = [
    { name: 'Mengisi Aplikasi Online', status: 'Passed' },
    { name: 'Seleksi Administrasi', status: 'Passed' },
    { name: 'Seleksi TPD & POF', status: 'Progress' },
    { name: 'Seleksi TPU, TPK & Bahasa Inggris', status: 'Disqualified' },
    { name: 'Seleksi Tes Psikologi, IGD & Wawancara Psikologi', status: 'Pending' },
    { name: 'Seleksi Tes Kesehatan & Psikiatri', status: 'Pending' },
    { name: 'Seleksi Wawancara Akhir', status: 'Pending' },
    { name: 'Pengumuman Akhir', status: 'Pending' },
  ];

  const lineClasses = {
    passed: 'border-t-green-500',
    progress: 'border-t-[#4142D8]',
    disqualified: 'border-t-red-600',
    pending: 'border-t-gray-300',
  };

  const iconType = {
    passed: <PassedIcon />,
    progress: <ProgressIcon />,
    disqualified: <NotSuitableIcon />,
    pending: <PendingIcon />,
  };

  const iconClasses = {
    passed: 'bg-green-500',
    progress: 'bg-[#4142D8]',
    disqualified: 'bg-red-600',
    pending: 'bg-gray-300',
  };

  const badgeClasses = {
    passed: 'bg-green-50 text-green-600 ',
    progress: 'bg-[#4142D8] text-white',
    disqualified: 'bg-red-600 text-white',
    pending: 'bg-white text-gray-500 border-dashed border border-gray-500',
  };

  const textClasses = {
    passed: 'text-slate-600',
    progress: 'text-[#4142D8] font-semibold',
    disqualified: 'text-red-600 font-semibold',
    pending: 'text-gray-500',
  };

  return (
    <div className="space-y-4">
      <div className="grid grid-cols-8">
        {steps.map((item, index) => {
          const statusKey = item.status.toLowerCase();

          return (
            <div key={index} className={clsx('border-t-2 last:border-t-white h-40 relative', lineClasses[statusKey])}>
              <div
                className={clsx(
                  'w-8 h-8 rounded-full flex items-center justify-center absolute -inset-y-5 left-0 border-4 border-white box-content',
                  iconClasses[statusKey]
                )}
              >
                {iconType[statusKey]}
              </div>

              <div className="absolute w-32 -left-11 inset-y-7 space-y-2">
                <div className="flex items-center justify-center">
                  <p className={clsx('text-[10px] py-1 px-2.5 rounded-full', badgeClasses[statusKey])}>{item.status}</p>
                </div>
                <p className={clsx('text-center text-xs', textClasses[statusKey])}>{item.name}</p>
              </div>
            </div>
          );
        })}
      </div>
    </div>
  );
}

